<template>
  <div class="content">
    <el-form
      status-icon
      ref="formLabel"
      label-width="100px"
      class="demo-ruleForm"
      :label-position="labelPosition"
      :rules="rules"
      :model="formLabel"
    >
      <el-form-item label="订单号" prop="order">
        <el-input v-model="formLabel.order" placeholder="请输入订单编号" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="患者手机号" prop="ptel">
        <el-input v-model="formLabel.ptel" placeholder="请输入患者手机号码" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="医生手机号" prop="dtel">
        <el-input v-model="formLabel.dtel" placeholder="请输入要更换的医生的手机号" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('formLabel')" class="subBtn">提交</el-button>
        <el-button @click="reset('formLabel')" >清空信息</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {rules} from '../../utils/formCheck'

export default {
  data() {
    rules['ptel'] = rules['tel']
    rules['dtel'] = rules['tel']
    return {
      labelPosition: 'right',
      formLabel: {
        order: '',
        ptel: '',
        dtel: ''
      },
      rules: rules
    }
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch('ModifyOrderDoctor', this.formLabel).then((response) => {
            if (response.code === 0) {
              alert('修改成功')
            }
          })
        } else {
          console.log('error submig')
          return false
        }
      })
    },
    reset(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style scoped>
  .content {
    background-color: #ffffff;
    width: auto;
    padding: 20px;
  }
  .subBtn {
    width: 150px;
  }
</style>



